.landing {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.ingLandPage {
    width: 50%;
    height: 100%;
    background-color: blueviolet;
}

.textoLandPage {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 120px;
    background-color: aqua;
}

.welcomeMsg {
    display: flex;
    /*  padding: 0.5rem; */
    color: rgb(6, 6, 75);
    font-style: italic;
    /* font-size: calc(5rem+0.8vw); */
    font-weight: 700;
    text-shadow: 6px 4px 5px rgba(0, 0, 0, 0.43);
}

.welcomeMsg2 {
    display: flex;
    padding: 0px;
    color: rgb(6, 6, 75);
    /*  font-style: italic; */
    /* font-size: calc(2rem+0.8vw); */
    font-weight: 700;
    text-shadow: 6px 4px 5px rgba(0, 0, 0, 0.43);
}

.homeButton {
    color: rgb(231, 218, 218);
    background-color: #ce5249;
    font-size: 17px;
    text-align: center;
    padding: 10px 25px;
    margin: 15px;
    border-radius: 3px;
    box-shadow: 3px 3px 5px 1px rgb(112, 44, 44);
    cursor: pointer;
}

.homeButton:hover {
    color: #ce5249;
    background-color: rgb(231, 218, 218);
}